import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import '@bbva-web-components/bbva-link';
import '@cells-components/cells-heading';
import styles from './bbva-header-multi-step-styles.js';
/**
Multistep headers are used to create hiearchy and organization within a multistep process.

Example:

With steps:
``` html
<bbva-header-multi-step
  heading="Title"
  step="2"
  total-steps="3">
</bbva-header-multi-step>
```

Without steps:
``` html
<bbva-header-multi-step
  heading="Title">
</bbva-header-multi-step>

```

### Custom Properties
| Selector                        | Property or Custom Property | CSS Variable or value                            | Value                                                                                                        | Fallback                                          |
| :------------------------------ | :-------------------------- | :----------------------------------------------- | :----------------------------------------------------------------------------------------------------------- | :------------------------------------------------ |
| :host                           | --_padding                  | --bbva-header-multi-step-padding                 | calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem) |                                                   |
| :host                           | --_height                   | --bbva-header-multi-step-height                  | 3.5rem                                                                                                       |                                                   |
| :host                           | --_background-color         | --bbva-header-multi-step-background-color        | --colorsPrimaryCore                                                                                          | foundations.colors.primaryCore                    |
| :host                           | --_title-color              | --bbva-header-multi-step-title-color             | --colorsPrimaryCoreLightened                                                                                 | foundations.colors.primaryCoreLightened           |
| :host                           | --_title-font-size          | --bbva-header-multi-step-font-size               | --typographyTypeSmall                                                                                        | foundations.typography.typeSmall                  |
| :host                           | --_title-font-weight        | --bbva-header-multi-step-font-weight             | --fontFacePrimaryMediumFontWeight                                                                            | foundations.fontFacePrimary.medium.fontWeight     |
| :host                           | --_title-line-heigh         | --bbva-header-multi-step-line-height             | --lineHeightTypeSmall                                                                                        | foundations.lineHeight.typeSmall                  |
| :host                           | --_edit-color               | --bbva-header-multi-step-edit-color              | --colorsPrimarySubdued                                                                                       | foundations.colors.primarySubdued                 |
| :host                           | --_steps-font-weight        | --bbva-header-multi-step-steps-font-weight       | --fontFacePrimaryBookItalicFontWeight                                                                        | foundations.fontFacePrimary.bookItalic.fontWeight |
| :host                           | --_steps-font-style         | --bbva-header-multi-step-steps-font-style        | --fontFacePrimaryBookItalicFontStyle                                                                         | foundations.fontFacePrimary.bookItalic.fontStyle  |
| :host([layout='active'])        | --_background-color         | --bbva-header-multi-step-active-background-color | --colorsPrimaryCoreLightened                                                                                 | foundations.colors.primaryCoreLightened           |
| :host([layout='active']) .steps | color                       | --bbva-header-multi-step-active-steps-color      | --colorsSecondary500                                                                                         | foundations.colors.secondary500                   |
| :host([layout='active']) .title | color                       | --bbva-header-multi-step-active-title-color      | --colorsSecondary600                                                                                         | foundations.colors.secondary600                   |
| .title                          | text-transform              | --bbva-header-multi-steps-text-transform         | uppercase                                                                                                    |                                                   |
| .steps                          | margin-left                 | --bbva-header-multi-step-steps-margin-left       | calc(((1 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1em)  |                                                   |
> Styling documentation generated by Cells CLI

@customElement bbva-header-multi-step
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsI18nMixin
*/
export class BbvaHeaderMultiStep extends cellsI18nMixin(LitElement) {
  static get is() {
    return 'bbva-header-multi-step';
  }

  static get properties() {
    return {
      /*
       * String to use as a title of the component
       */
      heading: {
        type: String,
      },
      /*
       * Number that represents the current step in the multistep process
       */
      step: {
        type: Number,
      },
      /*
       * Number that represents the total steps of the process.
       */
      totalSteps: {
        type: Number,
        attribute: 'total-steps',
      },
      /*
       * Boolean attribute that makes the Edit link not show if needed.
       */
      noLink: {
        type: Boolean,
        attribute: 'no-link',
      },
      /*
       * Number that represent the level of the title heading. Goes from 1 through 6.
       */
      headingLevel: {
        type: Number,
        attribute: 'heading-level',
      },
      /*
       * String that points to the translation String of edit
       */
      edit: {
        type: String,
      },
      /*
       * String that points to the translation String of steps
       */
      steps: {
        type: String,
      },
    };
  }

  constructor() {
    super();

    this.headingLevel = 0;
    this.edit = 'bbva-header-multi-step-edit-button';
    this.steps = 'bbva-header-multi-step-steps';
  }

  static get styles() {
    return [styles, getComponentSharedStyles('bbva-header-multi-step-shared-styles')];
  }

  get _steps() {
    return this.totalSteps && this.step
      ? html`
          <span class="steps"
            >(${this.t(this.steps, null, { step: this.step, totalSteps: this.totalSteps })})</span
          >
        `
      : html``;
  }

  render() {
    return html`
      <slot></slot>
      <div>
        <cells-heading class="title" level="${this.headingLevel}">${this.heading}</cells-heading>
        ${this._steps}
      </div>
      ${this.noLink
        ? html``
        : html`
            <bbva-link class="edit">${this.t(this.edit)}</bbva-link>
          `}
    `;
  }
}

customElements.define(BbvaHeaderMultiStep.is, BbvaHeaderMultiStep);
